@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600;700&display=swap);
:root {
  --color-primary: #da0037;
  --color-text-light: #ededed;
  --color-text-dark: #262626;
  --color-semi-dark: #444444;
  --color-dark: #171717;
  --neon-text-color: #f40;
  --neon-border-color: #ededed;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  background-color: #171717;
  background-color: var(--color-dark);
  color: rgb(38, 38, 38);
}
a {
  color: inherit;
  text-decoration: none;
}

.app__container--main {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

nav {
  background-color: var(--color-primary);
  opacity: 0.8;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-text-dark);
  box-shadow: 0px 5px 12px var(--color-dark);
}
.navbar__container {
  /* min-height: 5vh; */
  width: 95%;
  max-width: 1200px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* border: 1px solid #fff; */
}
.nav__logo--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border:1px solid #fff; */
  -webkit-animation: flicker 6s alternate;
          animation: flicker 6s alternate;
  padding: 0.5rem 1rem;
  overflow: hidden;
  border-radius: 10px;
  cursor:pointer;
}

.logo {
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #fff;
  /* border:1px solid #fff; */
  text-shadow: 1px 1px 3px var(--color-text-light),
    -1px -1px 3px var(--color-text-dark);
}
.logo__subtitle {
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 0.5px 0.5px 1.5px var(--color-text-dark),
    -0.5px -0.5px 1.5px var(--color-text-light);
}

/* Set neon color */

.logo::selection {
  background-color: var(--neon-border-color);
  color: var(--neon-text-color);
}

.logo:focus {
  outline: none;
}

/* Animate neon flicker */
@-webkit-keyframes flicker {
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff,
      0 0 1rem var(--neon-border-color), inset 0 0 1rem var(--neon-border-color),
      0 0 2rem var(--neon-border-color), inset 0 0 2rem var(--neon-border-color);
  }

  20%,
  24%,
  55% {
    text-shadow: none;
    box-shadow: none;
  }
}
@keyframes flicker {
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff,
      0 0 1rem var(--neon-border-color), inset 0 0 1rem var(--neon-border-color),
      0 0 2rem var(--neon-border-color), inset 0 0 2rem var(--neon-border-color);
  }

  20%,
  24%,
  55% {
    text-shadow: none;
    box-shadow: none;
  }
}

.nav__search--form {
  display: grid;
  grid-template-columns: 9fr 1fr;
  align-items: center;
  justify-items: flex-end;
  width: 25%;
}
.nav__search--input {
  padding: 0.5rem;
  border: none;
  width: 0%;
  transition: 0.4s ease-in;
  opacity: 0;
  font-weight: 500;
  background-color: var(--color-primary);
}
.nav__search--input::-webkit-input-placeholder {
  color: var(--color-semi-dark);
}
.nav__search--input:-ms-input-placeholder {
  color: var(--color-semi-dark);
}
.nav__search--input::placeholder {
  color: var(--color-semi-dark);
}
.nav__search--input.active {
  width: 100%;
  border-bottom: 3px solid var(--color-text-dark);
  opacity: 1;
  outline: none;
  color: var(--color-text-light);
}
.nav__search--input:focus {
  border-bottom: 3px solid var(--color-text-light);
}
.nav__search--icon {
  font-size: 1.5rem;
  margin: 0 1rem;
  transition: 0.3s ease;
  cursor: pointer;
  color: #fff;
  text-shadow: 1px 1px 3px var(--color-text-light),
    -1px -1px 3px var(--color-text-dark);
}
.nav__search--icon:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.nav__search--icon:active {
  -webkit-transform: scale(1);
          transform: scale(1);
}
@media screen and (max-width:450px){
  .logo {
    font-size: 1rem;
    letter-spacing: 1px;
  }
  .logo__subtitle {
    font-size: 0.7rem;
    text-transform: uppercase;
    letter-spacing: 0.2px;
  }
  .nav__search--form {
    display: grid;
    grid-template-columns: 9fr 1fr;
    align-items: center;
    justify-items: flex-end;
    width: 50%;
  }
}
.loading__container {
    width: 100%;
    height: 90vh;
    color: var(--color-text-light);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease;
    padding: 1rem;
}
.loading__message {
    font-size: 3rem;
    text-transform: uppercase;
    text-align: center;
}
.loading__message--subtitle {
    font-size: 1.5rem;
    text-align: center;
}
.loading__message--subtitle--2 {
    font-size: 1rem;
    text-align: center;
    color: var(--color-primary);
}
@media screen and (max-width: 500px){
    .loading__message {
        font-size: 2rem;
        text-transform: uppercase;
        text-align: center;
    }
    .loading__message--subtitle {
        font-size: 1rem;
    }
}
.movies__container {
  width: 100%;
  min-height: 90vh;
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  place-items: center;
  padding: 1rem 0;
}
.search__movie--container {
  width: 200px;
  height: 250px;
  display: grid;
  grid-template-rows: 8fr 2fr;
  align-items: center;
  place-items: center;
}
.search__movie--image {
  height: 200px;
  cursor: pointer;
}
.search__movie--title {
  font-size: 1rem;
  text-decoration: none;
  text-align: center;
  text-overflow: clip;
  color: var(--color-text-light);
}

@media  screen and (max-width: 500px) {
  .search__movie--container {
    width: 300px;
    height: 350px;
    display: grid;
    grid-template-rows: 8fr 2fr;
    align-items: center;
    place-items: center;
  }
  .search__movie--image {
    height: 320px;
    cursor: pointer;
  }
}
.home__container {
  padding: 1rem;
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero__movie--container {
  width: 900px;
  height: 80vh;
  border-radius: 5px;
  overflow: scroll;
  grid-gap: 0.2rem;
  gap: 0.2rem;
  display: grid;
  grid-template-columns: 1fr 1.1fr;
  box-shadow: -2px 2px 10px var(--color-dark), 2px -2px 5px var(--color-dark);
}
.hero__movie--image--container {
  height: 80vh;
  border-radius: 5px;
  overflow: hidden;
}
.hero__movie--image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position:center;
  opacity: 1;
}

.hero__movie--info {
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.878);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  border-radius: 5px;
  text-align: center;
  display: grid;
  grid-template-rows:1fr 1fr 1fr;
  overflow: scroll;
}

@media screen and (max-width: 550px) {
  .hero__movie--container {
    width: 900px;
    height: 100%;
    border-radius: 5px;
    overflow: scroll;
    grid-gap: 0.2rem;
    gap: 0.2rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    box-shadow: -2px 2px 10px var(--color-dark), 2px -2px 5px var(--color-dark);
  }
  .hero__movie--info {
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.878);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
    border-radius: 5px;
    overflow: hidden;
    text-align: center;
    border: 2px solid var(--color-primary);
    display: grid;
    grid-template-rows: auto;
    overflow: scroll;
  }
  .hero__movie--info {
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.878);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
    border-radius: 5px;
    overflow: hidden;
    text-align: center;
    display: grid;
    grid-template-rows: auto;
    overflow: scroll;
  }
}
.movie__title {
  color: var(--color-text-light);
  font-size: 3rem;
  text-shadow: 1px 1px 3px var(--color-text-light),
    -1px -1px 3px var(--color-text-dark);
}
.movie__title:hover {
  color: var(--color-primary);
  font-size: 3rem;
}
.movie__tagline {
  font-size: 1rem;
  color: var(--color-primary);
  font-weight: 400;
  /* border: 1px solid #fff; */
  padding: 0.5rem 0;
}
.movie__overview {
  margin: auto 0;
  color: var(--color-text-light);
  font-weight: 400;
  /* border: 1px solid #fff; */
  margin-top: 1rem;
  overflow: scroll;
}
.movie__genres {
  color: var(--color-primary);
  text-transform: uppercase;
  margin-top: 1rem;
}
.movie__facts--container {
  /* border: 1px solid #fff; */
  width: 100%;
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  margin: 1rem 0;
  align-self: center;
}

.movie__fact--title {
  color: var(--color-primary);
  text-transform: uppercase;
  font-size: 1.2rem;
}
.movie__fact--info {
  color: var(--color-text-light);
  text-transform: uppercase;
  font-size: 1.5rem;
}
.movie__fact--title span {
  font-size: 0.8rem;
  color: var(--color-text-light);
  font-weight: 300;
}

